import React, {useRef, useEffect} from "react"
import anime from "animejs";
import {SectionLoading, ContainerLogo , ProgressBar} from "../styles/LoadingPageStyles";
import LogoLuna from "../img/MoonLoading.svg"

const LoadingPage = () => {
    const  title1 = useRef();
    const title2 = useRef();
    const BarProgress = useRef();
    const logomoon = useRef();
    useEffect(() => {
        // opacity letters
        anime({
            targets: [title1.current , title2.current],
            opacity: [0,1],
            duration:1500,
            delay:1500,
            easing: "easeOutSine"
        });
        // bar progress animation 
        anime({
            targets: BarProgress.current,
            opacity:[0,1],
            width: ["0","100%"],
            duration:3000,
            easing:"easeOutSine"
        })
        // logo mooon animation
        anime({
            targets: logomoon.current,
            opacity:[0,1],
            translateY:["-400","0"],
            duration:2000,
            easing:'easeOutBounce'
        })

    },[])

    return(
        <SectionLoading>
            <ContainerLogo>
                <h2 ref={title1}>Carlos</h2>
                <span ref={title2}>Jarero</span>
                <img src={LogoLuna} ref={logomoon} alt="Logo luna" />
            </ContainerLogo>
            <ProgressBar ref={BarProgress}/>
        </SectionLoading>
        )
}
export default LoadingPage;