import React,{useRef,useState,useEffect} from "react";
import anime from "animejs";
import MenuClosed from "../img/MenuClosed.svg"
import MenuOpen from "../img/MenuOpen.svg";
import {NavigationBar, Logo, MenuHamborguer} from "../styles/HeaderStyles"
const Header = () => {
    const Clickref = useRef(); 
    const [clickMenu, setclickMenu] = useState(false);
    const effectClick = anime({
        targets: Clickref.current,
        opacity: [0,1],
        duration: 300,
        easing: "easeInCubic",
        autoplay: true,
    })
    const handleclick = (e) => {
        e.preventDefault();
        setclickMenu(!clickMenu)
    }
    useEffect(() => {
        Clickref.current.onclick = effectClick.play;
    },[clickMenu])

    return(
        <NavigationBar>
            <Logo>
                CJ
            </Logo>
            <MenuHamborguer onClick={handleclick} 
            src={!clickMenu ? MenuClosed : MenuOpen }
            ref={Clickref}>
            </MenuHamborguer>         
        </NavigationBar>
    )
}
export default Header;