import {Colors} from "../styles/Colors.js";
import styled from "styled-components";

export const SectionLoading = styled.section`
    width: 100vw;
    height:100vh;

`;
export const ContainerLogo = styled.div`
    width: 90%;
    height: 30%;
    display:flex;
    justify-content:space-around;
    align-items:center;
    position:relative;
    top:25%;
    left:5%;
    h2{
        font-family:"MS Gothic";
        color:${Colors.title};
        font-size:25px;
        position:relative;
        top:6%;
        font-weight:700;
    }
    span {
        order:3;
        font-family:"MS Gothic";
        color:${Colors.main};
        font-size:25px;
        font-weight:700;
        position:relative;
        top:6%;

    }
    img{
        width:40%;
        height:40%;
    }
    @media (min-width:768px){
        width: 70%;
        left:15%;
        h2{
            font-size:40px;
        }
        span{
            font-size:40px;
        }
        img{
            width:40%;
            height:40%;
        }
    }
    @media (min-width:1024px){
        h2{
            font-size:50px;
        }
        span{
            font-size:50px;
            }
    }
    @media (min-width:1200px){
        width:35%;
        left:27.5%;
        h2{
            font-size:35px;
        }
        span{
            font-size:35px;
        }

    }
`;
export const ProgressBar = styled.div`
    width: 100%;
    height:.5%;
    background-color:${Colors.main};
    position: relative;
    top:16%;
    @media(min-width:1200px){
        top:17%;
    }
`;